
import React, { Suspense, useEffect, useRef, useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import * as THREE from "three"
import { Canvas, useFrame, useLoader } from "@react-three/fiber"
import { Tween,ScrollTrigger } from 'react-gsap';
import './App.scss';
import {Navbar, Nav} from 'react-bootstrap';
import Card from './component/Card';
import Panda from './component/Panda';
import Dd from './component/Dd';
import { Route } from 'react-router-dom';
import appBg from './imgs/appBg9.png';
import { faPhone, faComment, faImage, faCamera } from '@fortawesome/free-solid-svg-icons';
import cloud1 from './imgs/cloud1.png';
import cloud2 from './imgs/cloud2.png';
import cloud3 from './imgs/cloud3.png';
import cloud4 from './imgs/cloud4.png';
import cloud5 from './imgs/cloud5.png';
import iphone from './imgs/iphone.png';
import img1 from './imgs/hoverImg1.png';
import img2 from './imgs/panda4.png';
import img3 from './imgs/hoverImg5.png';
import img4 from './imgs/ddbg4.png';
import dispOne from "./imgs/displacement/hover1.jpg";
import dispTwo from "./imgs/displacement/hover2.png";
import { useHistory } from 'react-router-dom';


import imageFadeMaterial from './ImageFadeMaterial';



function App() {


  let [scroll, scrollChange] = useState('header');
  let [mobileBtn, mobileBtnChange] = useState(false);
  let [cloud, cloudChange] = useState([cloud1, cloud2, cloud3, cloud4, cloud5, cloud1, cloud2, cloud3, cloud4, cloud5]);
  let history = useHistory();

  useEffect(() => {
    window.addEventListener('scroll', () => {
      if(window.scrollY > 50) {
        scrollChange('header headerAdd')
      }else if(window.scrollY <= 50) {
        scrollChange('header')
      }
    })
  }, [scroll])

  const homeRef = useRef();
  const skillRef = useRef();
  const pandaRef = useRef();
  const ddRef = useRef();



  function Clock() {
    let date = new Date();
    let month = date.getMonth();
    let clockDate = date.getDate();
    let day = date.getDay();
    let week = ['일요일', '월요일', '화요일', '수요일', '목요일', '금요일', '토요일'];
    let hours = date.getHours();
    let minutes = date.getMinutes();

    return (
      <h2>
        {hours < 10 ? `0${hours}` : hours}:{minutes < 10 ? `0${minutes}` : minutes}<br />
        <span>{month + 1}월 {clockDate}일 {week[day]}</span>
      </h2>
    )
  }

  function FadingImageOne() {
    const ref = useRef()
    const [texture1, texture2, dispTexture] = useLoader(THREE.TextureLoader, [img1, img2, dispTwo])
    const [hovered, setHover] = useState(false)
    useFrame(() => (ref.current.dispFactor = THREE.MathUtils.lerp(ref.current.dispFactor, !!hovered, 0.1)))
    return (
      <mesh onPointerMove={(e) => setHover(true)} onPointerOut={(e) => setHover(false)} onClick={() => {history.push('/panda')}} >
        <planeGeometry />
        <imageFadeMaterial ref={ref} tex={texture1} tex2={texture2} disp={dispTexture} />
      </mesh>
    )
  }

  function FadingImageTwo() {
    const ref = useRef()
    const [texture1, texture2, dispTexture] = useLoader(THREE.TextureLoader, [img3, img4, dispOne])
    const [hovered, setHover] = useState(false)
    useFrame(() => (ref.current.dispFactor = THREE.MathUtils.lerp(ref.current.dispFactor, !!hovered, 0.1)))
    return (
      <mesh onPointerMove={(e) => setHover(true)} onPointerOut={(e) => setHover(false)} onClick={() => {history.push('/15dd')}} >
        <planeGeometry />
        <imageFadeMaterial ref={ref} tex={texture1} tex2={texture2} disp={dispTexture} />
      </mesh>
    )
  }


  return (
    <>
    <Route exact path="/">
      <Navbar expand="lg" className={scroll}>
        <Navbar.Brand className="header-title text-light">PORTFOLIO</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto header-items" >
            <Nav.Link className="header-item text-light" 
            onClick={() => {
              homeRef.current.scrollIntoView({
                behavior: "smooth",
              });
            }}
            >Home</Nav.Link>
            <Nav.Link className="header-item text-light" onClick={() => {
              skillRef.current.scrollIntoView({
                behavior: "smooth",
              });
            }}>Skill</Nav.Link>
            <Nav.Link className="header-item text-light" onClick={() => {
              pandaRef.current.scrollIntoView({
                behavior: "smooth",
              });
            }}>판다마켓</Nav.Link>
            <Nav.Link className="header-item text-light" onClick={() => {
              ddRef.current.scrollIntoView({
                behavior: "smooth",
              });
            }}>15일배송</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

      <section ref={homeRef} className="app-mobile">
        <div className="mobile">
          <div className="top"></div>
          <div className="bottom" onClick={() => {mobileBtnChange(!mobileBtn)}}></div>
          { mobileBtn === false ?
            <div className="screen">
              <img src={appBg} alt="appBg" />
            </div>
            : null
          }

          { mobileBtn === true ?
            <div className="screenActive">
              <img src={appBg} alt="appBg" />
                <div className="clock">
                  <Clock />
                </div>
                <ul>
                  <li>
                    <FontAwesomeIcon icon={faPhone} className="mobilePhone"/>
                  </li>
                  <li>
                    <FontAwesomeIcon icon={faComment} className="mobileComment" />
                  </li>
                  <li>
                    <FontAwesomeIcon icon={faImage} className="mobileImage" />
                  </li>
                  <li>
                    <FontAwesomeIcon icon={faCamera} className="mobileCamera" />
                  </li>
                </ul>
            </div>
            : null
          }
        </div>
        <div className="clouds">
          {cloud.map((el, i) => {
            return (
              <img src={el} className={'cloud' + i} key={i} alt="clouds" />
            )
          })}
        </div>
      </section>

      <section ref={skillRef} className="skills">
        <ScrollTrigger start="0px center" end="200px center" scrub={0.9}>
          <Tween
            to={{
              x: '0vw',
            }}
          >
            <div className="skillBg1"></div>
          </Tween>
      </ScrollTrigger>

      <ScrollTrigger start="800px center" end="1200px center" scrub={0.9}>
          <Tween
            to={{
              y: '300px',
              opacity: '0',
            }}
          >
          <div className="skillPhone">
            <div>
              <Card />
            </div>
            <img src={iphone} alt="iphone" />
          </div>
          </Tween>
      </ScrollTrigger>

      <ScrollTrigger start="400px center" end="600px center" scrub={0.9}>
          <Tween
            to={{
              x: '0vw',
            }}
          >
            <div className="skillBg2"></div>
          </Tween>
      </ScrollTrigger>


      <ScrollTrigger start="1200px center" end="1500px center" scrub={0.9}>
          <Tween
            from={{
              opacity: '0',
            }}
          >
            <div className="skillBg3"></div>
          </Tween>
      </ScrollTrigger>


      


      </section>

      <section className="portfolio">
        <div className="row">
          <div ref={pandaRef} className="col-12 portOne">
            <h1>믿을 수 있는 판다마켓</h1>
            <p>
              비싸게 산 명품을 팔때 가격이 너무 차이나서 허탈한 경험이 있다면?<br />
              중고 명품을 사고 싶은데 개인 거래는 믿을 수 없어서 중고 매장에 가본적이 있다면?<br />
              이제 걱정 하지 마세요! 판다마켓에서 진품인지 검증해주니까 원하는 거래를 할 수
              있어요<br /> 
              믿을 수 있는 개인 중고명품 거래 플랫폼 판다마켓
            </p>
          </div>
          <div className="col-12 portOne oneHover">
            <Canvas camera={{ position: [2, 0, 2], fov: 23 }} className="port-hoverOne" >
              <Suspense fallback={null} >
                <FadingImageOne />
              </Suspense>
            </Canvas>
          </div>
        </div>
        <div className="row">
          <div ref={ddRef} className="col-12 portTwo">
          <h1>시간으로 할인받자! <br />15일 배송</h1>
            <p>
              빠른 배송은 생활을 편리하게 합니다<br />
              15일 배송은 생활을 풍성하게 합니다<br />
              미리 주문 하면 더 많이 할인 받는 15일 배송<br />
              이젠 시간으로 할인받는 15일 배송을 다운받아보세요<br />
              
              
            </p>
          </div>
          <div className="col-12 portTwo twoHover">
            <Canvas camera={{ position: [-2, 0, 2], fov: 23 }} className="port-hoverTwo" >
              <Suspense fallback={null} >
                <FadingImageTwo />
              </Suspense>
            </Canvas>
          </div>
        </div>
      </section>

      </Route>

      <Route path='/panda'>
        <Panda />
      </Route>
      

      <Route path='/15dd'>
        <Dd />
      </Route>
    </>
  )


}




export default App;
