import React, { useState } from 'react'
import {
  useTransition,
  useSpring,
  useChain,
  config,
  animated,
  useSpringRef,
} from '@react-spring/web'
import Carddata from './carddata';
import styles from './styles.module.scss'

export default function Card() {


  const [open, set] = useState(false);
 
  const springApi = useSpringRef();
  const {size, ...rest} = useSpring({
    ref : springApi,
    config : config.stiff,
    from: {size: '60%', background: 'none'},
    to: {
      size: open ? '80%' : '60%',
      background: open ? 'none' : 'none'
    },
  })

  const transApi = useSpringRef();
  const transition = useTransition(open ? Carddata : [], {
    ref : transApi,
    trail: 1000 / Carddata.length,
    from : {opacity: 0, scale: 0},
    enter : {opacity: 1, scale: 1},
    leave : {opacity: 0, scale: 0},
  })
  
  useChain(open ? [springApi, transApi] : [transApi, springApi], [
    0,
    open ? 0.1 : 0.6,
  ])

  return (
    <>
    <div className={styles.wrapper}>
      <animated.div
        style = {{...rest, width: size, height: size}}
        className={styles.container}
        onClick={() => set(open => !open)}>
          {open === false ?
          <div className={styles.middle}>
              <h1 className={styles.title}>SKILLS</h1>
          </div>
          : null}
        {transition((style, item) => (
          <animated.div 
            className={style.item}
            style={
              {
                ...style,
                width: item.width,
                height: item.height,
                backgroundImage: item.css,
                backgroundColor: item.background,
                borderRadius: item.borderRadius,
                backgroundRepeat: item.repeat,
                objectFit: item.size,
                backgroundPosition: item.position,
                WebkitBoxShadow: item.webkitBoxShadow,
                MozBoxShadow: item.mozBoxShadow,
                boxShadow: item.boxShadow,
                margin: item.margin
              }}
          >
        </animated.div>
        ))}

      </animated.div>
    </div>
    </>
  )

}